:root {
  --primary: #00d4ff;
  --primary-dark: #00a8cc;
  --bg-dark: #0f0f19;
  --bg-card: #1a1a2e;
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.6);
}

.home {
  min-height: 100vh;
  background: linear-gradient(180deg, #0f0f19 0%, #1a1a2e 100%);
  position: relative;
  overflow-x: hidden;
}

.home::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 400px;
  background: radial-gradient(
    ellipse at top,
    rgba(0, 212, 255, 0.15) 0%,
    transparent 60%
  );
  pointer-events: none;
  z-index: 0;
}

.app-header {
  position: relative;
  z-index: 1;
  padding: 3rem 1rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center; /* Standard horizontal centering for column flex */
  justify-content: center; /* Vertical centering within the header height */
  text-align: center;
}

.header-content {
  width: 100%;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center; /* Ensures everything inside is centered */
}
.header-title {
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center; /* Centers the Motion div content */
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centers Icon and H1 */
  gap: 1rem;
  margin-bottom: 1rem;
  width: 100%;
}

/* Updated H1 to remove any browser-default alignment bias */
.logo h1 {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  background: linear-gradient(135deg, #fff 0%, var(--primary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
  padding: 0;
}

/* Ensure tagline occupies its own centered block */
.tagline {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-top: 0.5rem;
}

.logo-icon {
  width: 72px;
  height: 72px;
  background: linear-gradient(
    135deg,
    var(--primary) 0%,
    var(--primary-dark) 100%
  );
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow:
    0 8px 32px rgba(0, 212, 255, 0.4),
    0 0 0 1px rgba(0, 212, 255, 0.2);
  animation: iconPulse 3s ease-in-out infinite;
}

@keyframes iconPulse {
  0%,
  100% {
    box-shadow:
      0 8px 32px rgba(0, 212, 255, 0.4),
      0 0 0 1px rgba(0, 212, 255, 0.2);
  }
  50% {
    box-shadow:
      0 8px 40px rgba(0, 212, 255, 0.6),
      0 0 0 1px rgba(0, 212, 255, 0.4);
  }
}

.logo-scan-line {
  position: absolute;
  top: 50%;
  left: 12px;
  right: 12px;
  height: 2px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
  animation: scanLine 2s ease-in-out infinite;
}

@keyframes scanLine {
  0%,
  100% {
    transform: translateY(-12px);
    opacity: 0.3;
  }
  50% {
    transform: translateY(12px);
    opacity: 1;
  }
}

/* Ensure the text doesn't break nicely */
.logo h1 {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
  background: linear-gradient(135deg, #fff 0%, var(--primary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
}

.tagline {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin-top: 0.5rem;
}

.main-content {
  position: relative;
  z-index: 1;
  padding: 2rem 1rem;
  min-height: calc(100vh - 250px);
}

.error-banner {
  max-width: 600px;
  margin: 0 auto 2rem;
  background: linear-gradient(
    135deg,
    rgba(239, 68, 68, 0.15) 0%,
    rgba(239, 68, 68, 0.1) 100%
  );
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 16px;
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  box-shadow: 0 4px 16px rgba(239, 68, 68, 0.2);
}

.error-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
}

.error-content svg {
  font-size: 1.5rem;
  color: #ef4444;
  flex-shrink: 0;
}

.error-content p {
  margin: 0;
  color: #fff;
  font-weight: 500;
}

.error-close {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #fff;
  flex-shrink: 0;
}

.error-close:hover {
  background: rgba(255, 255, 255, 0.15);
}

.scan-section {
  max-width: 400px;
  margin: 0 auto 4rem;
  text-align: center;
}

.scan-button {
  position: relative;
  width: 100%;
  padding: 0;
  border: none;
  background: linear-gradient(
    135deg,
    var(--primary) 0%,
    var(--primary-dark) 100%
  );
  border-radius: 20px;
  cursor: pointer;
  overflow: hidden;
  box-shadow:
    0 8px 32px rgba(0, 212, 255, 0.4),
    0 0 0 1px rgba(0, 212, 255, 0.2);
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

.scan-button:hover {
  transform: translateY(-4px);
  box-shadow:
    0 12px 48px rgba(0, 212, 255, 0.5),
    0 0 0 1px rgba(0, 212, 255, 0.3);
}

.scan-button:active {
  transform: translateY(-2px);
}

.scan-button-content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
  padding: 2.5rem 2rem;
  color: #fff;
}

.scan-button-content svg {
  font-size: 3rem;
}

.scan-button-content span {
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.02em;
}

.scan-button-glow {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.2) 0%,
    transparent 70%
  );
  animation: glowPulse 3s ease-in-out infinite;
}

@keyframes glowPulse {
  0%,
  100% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}

.scan-hint {
  margin-top: 1.5rem;
  color: var(--text-secondary);
  font-size: 0.9375rem;
}

.loading-section {
  text-align: center;
  padding: 4rem 2rem;
}

.loading-spinner-large {
  width: 64px;
  height: 64px;
  border: 4px solid rgba(0, 212, 255, 0.2);
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 1.5rem;
}

.loading-section p {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0;
  font-weight: 500;
}

.product-actions {
  max-width: 600px;
  margin: 0 auto 2rem;
  padding: 0 1rem;
  display: flex;
  gap: 1rem;
}

.action-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem 1.5rem;
  border-radius: 14px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.action-button.primary {
  background: linear-gradient(
    135deg,
    var(--primary) 0%,
    var(--primary-dark) 100%
  );
  color: #fff;
  box-shadow: 0 4px 16px rgba(0, 212, 255, 0.3);
}

.action-button.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 212, 255, 0.4);
}

.action-button.secondary {
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.action-button.secondary:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(255, 255, 255, 0.15);
}

.action-button svg {
  font-size: 1.25rem;
}

.app-footer {
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 2rem 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  background: rgba(0, 0, 0, 0.2);
}

.app-footer p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 0.875rem;
}

.app-footer a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s ease;
}

.app-footer a:hover {
  color: #fff;
}

@media (max-width: 640px) {
  .app-header {
    padding: 2rem 1rem 1.5rem;
  }

  .logo h1 {
    font-size: 2rem;
  }

  .tagline {
    font-size: 1rem;
  }

  .product-actions {
    flex-direction: column;
  }

  .action-button {
    width: 100%;
  }
}

.ai-button {
  background: linear-gradient(
    135deg,
    #8b5cf6 0%,
    #6366f1 100%
  ); /* Purple/Indigo for AI */
  color: white;
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.3);
  border: none;
}

.ai-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 92, 246, 0.4);
}

@media (max-width: 640px) {
  .product-actions {
    flex-direction: column;
    gap: 0.75rem;
  }
}
